import React, {useEffect, useState} from 'react';
import PropTypes from 'prop-types';
import {makeStyles} from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import {generateContentfulClient} from "../../contentful-api/clientActions";
import {filterFeaturedPosts, loadContentfulPosts} from "../../contentful-api/postActions";
import FeaturedPost from "./FeaturedPost";
import BlogHomeArticles from "./BlogHomeArticles";
import Sidebar from "./Sidebar";
import withStyles from "@material-ui/core/styles/withStyles";
import mosqueImg from "../../mosque.jpg";
import {loadContentfulPrayerTimes} from "../../contentful-api/prayerTimeActions";
import SimpleTable from "./SimpleTable";

const useStyles = makeStyles((theme) => ({
    mainFeaturedPost: {
        position: 'relative',
        backgroundColor: theme.palette.grey[800],
        color: theme.palette.common.white,
        marginBottom: theme.spacing(4),
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat',
        backgroundPosition: 'center',
    },
    overlay: {
        position: 'absolute',
        top: 0,
        bottom: 0,
        right: 0,
        left: 0,
        backgroundColor: 'rgba(0,0,0,.3)',
    },
    mainFeaturedPostContent: {
        position: 'relative',
        padding: theme.spacing(3),
        [theme.breakpoints.up('md')]: {
            padding: theme.spacing(6),
            paddingRight: 0,
        },
    },
}));


/** Gets today's short date, in the format "1 Jan" or "3 Feb" */
const getShortDate = () => {
    const today = new Date();
    return today.getDate() + " " + today.toLocaleDateString('en-GB', {month: 'short'})
}

/** Gets today's full date, in the format "Wed 9 Oct 2020" */
const getFullDate = () => {
    var options = {  weekday: 'short', year: 'numeric', month: 'long', day: 'numeric'};
    return new Date().toLocaleDateString('en-GB', options);

}

export default function MainFeaturedPost(props) {
    const classes = useStyles();
    const [adhanTimes, setAdhanTimes] = useState({})
    const [iqamaTimes, setIqamaTimes] = useState({})
    const shortDate = getShortDate()
    const fullDate = getFullDate()

    useEffect(() => {
        const fetchData = async () => {
            const client = generateContentfulClient()
            const prayerTimes = await loadContentfulPrayerTimes(client)
            await setAdhanTimes(prayerTimes.adhanTimes)
            await setIqamaTimes(prayerTimes.iqamaTimes)
        }
        fetchData();
    }, []); // passing an empty array as second argument triggers the callback in useEffect only after the initial render thus replicating `componentDidMount` lifecycle behaviour


    return (
        <Paper className={classes.mainFeaturedPost} style={{backgroundImage: `url(${mosqueImg})`}}>
            {/* Increase the priority of the hero background image */}
            {<img style={{display: 'none'}} src={mosqueImg} alt={"Picture of a mosque"}/>}
            <div className={classes.overlay}/>
            <Grid container>
                <Grid item md={6}>
                    <div className={classes.mainFeaturedPostContent}>
                        <Typography component="h1" variant="h3" color="inherit" gutterBottom>
                            Prayer Times
                        </Typography>
                        <Typography variant="h5" color="inherit">
                            <SimpleTable adhanTimes={adhanTimes[shortDate]} iqamaTimes={iqamaTimes[shortDate]} date={fullDate}/>
                        </Typography>
                    </div>
                </Grid>
            </Grid>
        </Paper>
    );
}

MainFeaturedPost.propTypes = {
    post: PropTypes.object,
};

