import React from 'react';
import PropTypes from 'prop-types';
import {makeStyles} from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Hidden from '@material-ui/core/Hidden';
import {PATH_TO_POST} from "../../helpers/constants";


const useStyles = makeStyles((theme) => ({
    card: {
        display: 'flex',
    },
    cardDetails: {
        flex: 1,
    },
    cardMedia: {
        width: 200,
    },
    mobileCardMedia: {
        height: 140,
    },
    markdown: {
        ...theme.typography.body2,
        padding: theme.spacing(2, 0),
    },
}));

export default function UnfeaturedPost(props) {
    const classes = useStyles();
    const {post} = props;


    return (
        <Grid item xs={12} className={classes.markdown}>
            <CardActionArea component="a" href={`${PATH_TO_POST}/${post.id}/`}>
                <Hidden mdUp>
                    <CardMedia
                        className={classes.mobileCardMedia}
                        image={post.imageUrl}
                        title="Contemplative Reptile"
                    />
                </Hidden>
                <Card className={classes.card}>
                    <Hidden smDown>
                        <CardMedia className={classes.cardMedia} image={post.imageUrl} title={post.imageTitle}/>
                    </Hidden>
                    <div className={classes.cardDetails}>
                        <CardContent>
                            <Typography gutterBottom component="h2" variant="h5">
                                {post.title}
                            </Typography>
                            <Typography variant="subtitle1" color="textSecondary">
                                {post.date}
                            </Typography>
                            <Typography variant="subtitle1" paragraph>
                                {post.description}
                            </Typography>
                            <Typography variant="subtitle1" color="primary">
                                Continue reading...
                            </Typography>
                        </CardContent>
                    </div>
                </Card>
            </CardActionArea>
        </Grid>
    );

}

UnfeaturedPost.propTypes = {
    post: PropTypes.object,
};
