/*Code related to loading the contentful posts */

/**Loads a single contentful post
 *
 * @param client: the contentful client
 * @param post_id: the id to the post
 * @returns The response of the fields.
 */
export const loadContentfulPost = async (client, post_id) => {
    const entries = await client.getEntries({
        'sys.id[in]': post_id
    })
    const post =  entries.items[0]
    return transformPostFormat(post)
}


/**Loads the contentful Blog Posts
 *
 * @param client
 * @returns list of blog posts formatted for the API
 */
export const loadContentfulPosts = async (client) => {

    const allContentTypes = await client.getContentTypes();
    const contentType = await allContentTypes.items.find(item => item.name === 'Blog Post');
    const contentTypeId = contentType.sys.id;
    const allRawPosts = await client.getEntries({content_type: contentTypeId, order: "-fields.publishDate"});
    return transformPostsFormat(allRawPosts.items)
}

const transformPostFormat = (post) => {
    return ({
            id: post.sys.id,
            title: post.fields.title,
            date: post.fields.publishDate,
            description: post.fields.description,
            imageUrl: post.fields.image.fields.file.url,
            imageTitle: post.fields.image.fields.title,
            featured: !!post.fields.featured,
            body: post.fields.body,
        })
}

/**Transforms the posts into a Post Object
 *
 * @param allRawPosts
 */
const transformPostsFormat = (allRawPosts) => {
    return allRawPosts.map((post) => {
        return transformPostFormat(post)
    })
}

/** Only return featured Posts
 *
 * @param allPosts
 * @returns {*}
 */
export const filterFeaturedPosts = (allPosts) => {
    return allPosts.filter(
        (post) =>
            post.featured
    )
}