import React, {Component} from 'react';
import {BrowserRouter, Switch, Route} from 'react-router-dom';
import Post from './components/Post';
import Base from "./components/blog/Base";
import Helmet from "react-helmet";
import Blog from "./components/blog/Blog";
import {PATH_TO_HOME, PATH_TO_POST} from "./helpers/constants";

class App extends Component {
    render() {
        return (
            <BrowserRouter>
                <div className="App">
                    <Base>
                        <Helmet title="FIC - Fine Islamic Centre"/>
                        <Switch>
                            <Route path={`${PATH_TO_POST}/:id`} component={Post}/>
                            <Route path={PATH_TO_HOME} component={Blog}/>
                        </Switch>
                    </Base>
                </div>
            </BrowserRouter>
        );
    }
}

export default App;
