import React, {Component} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import Header from './Header';
import Footer from './Footer';
import withStyles from "@material-ui/core/styles/withStyles";
import {PATH_TO_HOME} from "../../helpers/constants";


const useStyles = makeStyles((theme) => ({
    mainGrid: {
        marginTop: theme.spacing(3),
    },
}));

const sections = [
    {title: 'Prayer Times', url: PATH_TO_HOME},
    {title: 'Mosque', url: PATH_TO_HOME},
    {title: 'FAQs', url: PATH_TO_HOME},
    {title: 'Contact', url: PATH_TO_HOME}
];

class Blog extends Component {

    state = {
        posts: [],
        featuredPosts: []
    };

    render() {

        const classes = this.props.classes;

        return (
            <React.Fragment>
                <CssBaseline/>
                <Container maxWidth="lg">
                    <Header title="Fine Islamic Centre" sections={sections} link=""/>
                    <main>
                        {this.props.children}
                    </main>
                </Container>
                <Footer title="Fine Islamic Centre (FIC)"
                        description="Company Number: 12852476"/>
            </React.Fragment>
        );
    }
}


export default withStyles(useStyles)(Blog);
