import React, {Component} from 'react';
import Helmet from 'react-helmet';

import {generateContentfulClient} from "../contentful-api/clientActions";
import {loadContentfulPost} from "../contentful-api/postActions";
import {getMarkup} from "../helpers/markupActions";
import makeStyles from "@material-ui/core/styles/makeStyles";
import withStyles from "@material-ui/core/styles/withStyles";
import {Typography} from "@material-ui/core";
import Card from "@material-ui/core/Card";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import Grid from "@material-ui/core/Grid";
import "./Post.css"


const useStyles = makeStyles(theme => ({
    root: {
        padding: theme.spacing(6, 6),
        marginTop: theme.spacing(6),
    },
    card: {
        display: 'flex',
    },
    markdown: {
        ...theme.typography.body2,
        padding: theme.spacing(2, 0),
    },
}));

class Post extends Component {
    constructor(props) {
        super(props);

        this.state = {
            data: null
        };
    }

    componentWillMount = async () => {
        const client = generateContentfulClient();
        const post = await loadContentfulPost(client, this.props.match.params.id)
        this.setState({post})
    }

    render() {
        let title;
        let content;
        let description;
        let imageUrl;
        let imageTitle;

        if (this.state.post) {
            title = this.state.post.title;
            content = getMarkup(this.state.post.body);
            description = this.state.post.description;
            imageUrl = this.state.post.imageUrl;
            imageTitle = this.state.post.imageTitle;
        }

        const classes = this.props.classes;
        return (
            <Grid item xs={12} className={classes.markdown}>
                <Helmet title={title}/>
                <Card className={classes.card}>
                    <CardContent>
                        <Typography gutterBottom component="h2" variant="h5">
                            {title}
                        </Typography>
                        <Typography variant="subtitle1" color="textSecondary">
                            {description}
                        </Typography>
                        <CardMedia
                            component="img"
                            image={imageUrl}
                            title={imageTitle}
                        />
                        <Typography variant="subtitle1" paragraph>
                            {content}
                        </Typography>
                    </CardContent>
                </Card>
            </Grid>
        );
    }
}

export default withStyles(useStyles)(Post);
