/** Shortens the text if longer than the maxCharacters, and adds "..."
 *
 * @param text: the text to shorten
 * @param maxCharacters: default is 100
 * @returns {string}
 */
export const limitTextLength = (text, maxCharacters) => {
    maxCharacters = maxCharacters ? maxCharacters : 100
    return text.length < maxCharacters ? text : text.slice(0, maxCharacters - 3) + "..."
}