import React, {Component} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import MainFeaturedPost from './MainFeaturedPost';
import FeaturedPost from './FeaturedPost';
import BlogHomeArticles from './BlogHomeArticles';
import Sidebar from './Sidebar';
import {generateContentfulClient} from "../../contentful-api/clientActions";
import {filterFeaturedPosts, loadContentfulPosts} from "../../contentful-api/postActions";
import withStyles from "@material-ui/core/styles/withStyles";
import mosqueImg from "../../mosque.jpg"
import {PATH_TO_HOME} from "../../helpers/constants";


const useStyles = makeStyles((theme) => ({
    mainGrid: {
        marginTop: theme.spacing(3),
    },
}));

const mainFeaturedPost = {
    title: 'Prayer Times',
    description:
        "Fajr:\n" +
        "Dhuhr:\n" +
        "Asr:\n" +
        "Maghrib:\n" +
        "Isha:\n",
    image: mosqueImg,
    imgText: 'main image description',
};


const sidebar = {
    title: 'Address',
    description:
        'Fine Islamic Centre \n White City Mosque \n 95 Bloemfontein Rd, White City, \n London W12 7DA',
    archives: [
        {title: 'March 2020', url: PATH_TO_HOME},
        {title: 'February 2020', url: PATH_TO_HOME},
        {title: 'January 2020', url: PATH_TO_HOME}
    ],
    social: [
        {name: 'Twitter', icon: TwitterIcon, url: PATH_TO_HOME},
        {name: 'Facebook', icon: FacebookIcon, url: PATH_TO_HOME},
    ],
};

class Blog extends Component {

    state = {
        posts: [],
        featuredPosts: []
    };

    componentWillMount = async () => {
        const client = generateContentfulClient()
        const posts = await loadContentfulPosts(client)
        const featuredPosts = filterFeaturedPosts(posts)
        this.setState({posts, featuredPosts});
    }

    render() {

        const classes = this.props.classes;

        return (

            <div>
                <MainFeaturedPost post={mainFeaturedPost}/>
                <Grid container spacing={4}>
                    {this.state.featuredPosts && this.state.featuredPosts.slice(0, 2).map((post) => (
                        <FeaturedPost key={post.id} post={post}/>
                    ))}
                </Grid>
                <Grid container spacing={5} className={classes.mainGrid}>
                    <BlogHomeArticles title="Latest news" posts={this.state.posts}/>
                    <Sidebar
                        title={sidebar.title}
                        description={sidebar.description}
                        archives={sidebar.archives}
                        social={sidebar.social}
                    />
                </Grid>
            </div>

        );
    }
}


export default withStyles(useStyles)(Blog);
