import React from 'react';
import {makeStyles} from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Typography from "@material-ui/core/Typography";

const useStyles = makeStyles({
    table: {
        maxWidth: "80vw",
    },
    tableText: {
        color: "white"

    }
});

function createData(prayer, adhan, iqama) {
    return {prayer, adhan, iqama};
}

const defaultAdhanTimes = {"Fajr": "", "Sunrise": "", "Dhuhr": "", "Asr": "", "Maghrib": "", "Isha": ""}
const defaultIqamaTimes = {"Fajr": "", "Dhuhr": "", "Asr": "", "Maghrib": "", "Isha": ""}

const loadRows = (adhanTimes, iqamaTimes) => [
    createData('Fajr', adhanTimes.Fajr, iqamaTimes.Fajr),
    createData("Sunrise", adhanTimes.Sunrise, ""),
    createData('Dhuhr', adhanTimes.Dhuhr, iqamaTimes.Dhuhr),
    createData('Asr', adhanTimes.Asr, iqamaTimes.Asr),
    createData('Maghrib', adhanTimes.Maghrib, iqamaTimes.Maghrib),
    createData('Isha', adhanTimes.Isha, iqamaTimes.Isha),

];

export default function PrayerTable(props) {
    const classes = useStyles();
    const {date, adhanTimes, iqamaTimes} = props
    const rows = loadRows(adhanTimes ? adhanTimes : defaultAdhanTimes,
        iqamaTimes ? iqamaTimes : defaultIqamaTimes)

    return (
        <TableContainer>
            {date}
            <Table className={classes.table} size="small" aria-label="a dense table">
                <TableHead className={classes.table}>
                    <TableRow className={classes.table}>
                        <TableCell><Typography variant="h6"
                                               className={classes.tableText}>Prayer</Typography></TableCell>
                        <TableCell><Typography variant="h6" className={classes.tableText}>Adhan</Typography></TableCell>
                        <TableCell><Typography variant="h6" className={classes.tableText}>Iqama</Typography></TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>

                    {rows.map(row => (
                        <TableRow key={row.prayer}>
                            <TableCell><Typography variant="h6" className={classes.tableText}>{row.prayer}</Typography></TableCell>
                            <TableCell><Typography variant="h6"
                                                   className={classes.tableText}>{row.adhan}</Typography></TableCell>
                            <TableCell><Typography variant="h6"
                                                   className={classes.tableText}>{row.iqama}</Typography></TableCell>
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
    );
}