/*Code related to loading the contentful posts */


/**Loads the contentful Prayer times
 *
 * @param client
 * @returns {adhanTimes: {...,} iqamaTimes: {...}}
 */
export const loadContentfulPrayerTimes = async (client) => {

    const allContentTypes = await client.getContentTypes();
    const contentType = allContentTypes.items.find(item => item.name === 'prayer-times');
    const contentTypeId = contentType.sys.id;
    const allPrayerTimes = await client.getEntries({content_type: contentTypeId});
    return transformPrayerTimes(allPrayerTimes)
}

/**Transforms the prayer times into {
 * adhanTimes: {"1 Jan": {"Fajr": "04:03", "Sunrise": "07:02", "Dhuhr": "12:05", "Asr": "15:01", "Maghrib": "17:05", "Isha": "18:41"}, ...},
 * iqamaTimes: {"1 Jan": {"Fajr": "04:45", "Dhuhr": "13:30", "Asr": "15:15", "Maghrib": "17:15", "Isha": "19:00"}, ...}
 *
 * @param allPrayerTimes
 */
const transformPrayerTimes = (allPrayerTimes) => {
    return ({
            adhanTimes: allPrayerTimes.items.filter((prayerType) => !!prayerType.fields.isAdhan)[0].fields.prayerDate,
            iqamaTimes: allPrayerTimes.items.filter((prayerType) => !!prayerType.fields.isIqama)[0].fields.prayerDate
        })
}

